import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { createContext } from 'react'

export const APIContext = createContext();

export default function APIContextProvider(props) {
    
  const [allMovies, getAllMovies] = useState(null);
  const [allSeries,getAllSeries] = useState(null);
  
  async function getTrendingMovies() {
    let { data } = await axios.get("https://api.themoviedb.org/3/trending/movie/week?api_key=0ada3fcaa2e394ff1ad6ed7e6c5db6c5");
    getAllMovies(data.results);
  }
  async function getTrendingSeries(){
    let {data} = await axios.get("https://api.themoviedb.org/3/trending/tv/day?api_key=0ada3fcaa2e394ff1ad6ed7e6c5db6c5")
    getAllSeries(data.results)
  }
  

  
  useEffect(() => {
    getTrendingMovies();
   
    getTrendingSeries();

  }, [])
  


    return <APIContext.Provider value={{
        allMovies: allMovies,
        allSeries:allSeries
        }}>
        {props.children}
    </APIContext.Provider>
}
